export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDividerModule, NuiDocsModule, SrlcStage } from \"@nova-ui/bits\";\n\nimport { DividerExampleComponent, HorizontalDividersExampleComponent, VerticalDividersExampleComponent } from \"./index\";\n\nconst routes = [{\n    path: \"\",\n    component: DividerExampleComponent,\n    data: {\n        \"srlc\": {\n            \"stage\": SrlcStage.alpha,\n        },\n    },\n}];\n\n@NgModule({\n    imports: [\n        NuiDividerModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        DividerExampleComponent,\n        HorizontalDividersExampleComponent,\n        VerticalDividersExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class DividerModule {\n}\n";